.contain {
  width: 500px;
  height: 600px;
  background-color: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
}

.card-container {
  margin-top: 5rem;
  width: 350px;
  background-color: white;
  border-radius: 30px;
  box-shadow: 20px 0px 60px rgba(0, 0, 0, 0.2);
  background-color: transparent;
  perspective: 1000px;
}
.card-container .xbox {
  padding: 1rem 0 0 2rem;
  color: #fe992b;
  font-size: 1.2rem;
  font-weight: bolder;
}
.card-container .xbox:before {
  content: "<";
  padding-right: 10px;
  cursor: pointer;
}
.card-container .fish {
  min-height: 250px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.card-container .fish img {
  position: absolute;
  width: 20rem;
  top: 0;
  left: -80px;
  transform-style: preserve-3d;
  transition: all 0.75s ease-out;
  z-index: 5;
  filter: drop-shadow(2rem 2rem 2rem #c6bcd7);
  pointer-events: none;
}
.card-container .icons {
  position: absolute;
  width: 180px;
  height: 300px;
  top: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  background: linear-gradient(0.35turn, #f1eff4, #f5f5f5);
  border-top-right-radius: 30px;
}
.card-container .icons img {
  width: 2rem;
}
.card-container .icons div {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2rem;
  cursor: pointer;
  color: #293861;
  margin: 15px;
  width: 50px;
  height: 50px;
  background-color: #e9e9e9;
  border-radius: 15px;
}
.card-container .content {
  padding: 2rem;
}
.card-container .content .title {
  color: #ed9827;
  font-size: 1.12rem;
  font-weight: bolder;
}
.card-container .content .under {
  font-size: 0.7rem;
  color: #bdc2d0;
  font-weight: bold;
}
.card-container .content p {
  font-size: 0.8rem;
  padding-top: 1rem;
  color: #bdc2d0;
}
.card-container .content .product {
  padding-top: 1rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.card-container .content .product div {
  cursor: pointer;
}
.card-container .content .product button {
  padding: 0.1rem 0.5rem 0.1rem 0.5rem;
  background-color: #b5ce32;
  border: 0;
  color: white;
  font-size: 1rem;
  border-radius: 5px;
  outline: none;
  cursor: pointer;
}
.card-container .content .product span {
  padding: 10px;
}
.card-container .bottom {
  width: 80%;
  margin: auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 1rem;
}
.card-container .bottom h3 {
  color: #fe992b;
  position: relative;
}
.card-container .bottom h3:after {
  content: "The price";
  font-size: 0.5rem;
  color: #bdc2d0;
  font-weight: medium;
  position: absolute;
  bottom: -8px;
  left: 0;
}
.card-container .bottom button {
  outline: none;
  border: none;
  background: #b5ce32;
  width: 150px;
  padding-top: 0.7rem;
  padding-bottom: 0.7rem;
  border-radius: 15px;
  color: white;
  font-weight: bold;
  cursor: pointer;
  transition: all 330ms ease;
}
.card-container .bottom button:hover {
  background-color: #a5f785;
}